//

export default {
	props: {
		imageStyle: String,
		src: {
			type: String,
			default: '',
			required: true
		},
		saveDirectory: {
			type: String,
			default: '_downloads/'
		},
		errorImage: String,
		loadingImage: String
	},
	computed: {
		resource() {
			if (!this.isCached && this.error) {
				// 加载失败
				return this.errorImage;
			} else if (this.isCached) {
				return this.localFile;
			} else if (!this.isCached && !this.error) {
				// 正在加载
				return this.loadingImage;
			}
		}
	},
	created() {
		this.init();
	},
	data() {
		return {
			error: false,
			isCached: false,
			localFile: null
		};
	},
	methods: {
		async init() {














			this.isCached = true;
			this.localFile = this.src;

		},
		downloadFile(url) {
			return new Promise((resolve, reject) => {
				let savePath = this.saveDirectory + this.getFileName(url);
				let task = plus.downloader.createDownload(
					url,
					{
						filename: savePath
					},
					(download, status) => {
						if (status == 200) {
							resolve(savePath);
						} else {
							console.log('文件下载失败');
							reject();
						}
					}
				);
				task.start();
			});
		},
		isCache(url) {
			let savePath = this.saveDirectory + this.getFileName(url);
			return new Promise((resolve, reject) => {
				plus.io.resolveLocalFileSystemURL(
					savePath,
					entry => {
						resolve(savePath);
					},
					e => {
						console.log(e);
						resolve(null);
					}
				);
			});
		},
		getFileName(path) {
			return path.substring(path.lastIndexOf('/') + 1);
		},
		imageLoadError() {

			this.localFile = this.errorImage;

		}
	}
};
